<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <section>
    <!-- type -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.type') }}:</h4>
      <template v-for="(item, index) in types" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-type"
          :id="`type${index}`"
          autocomplete="off"
          :value="item.type"
          @click="(option.quantity = 1), slelectProduct(item.type)"
        />
        <label
          class="btn border-0 position-relative me-4"
          :for="`type${index}`"
        >
          {{ $t(item.title) }}
          <Circle2 :class="{ 'd-none': tempProduct.type !== item.type }" />
        </label>
      </template>
    </div>
    <!-- flavor -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.flavor') }}:</h4>
      <template v-for="(item, index) in tempProductList" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-flavor"
          :id="`flavor${index}`"
          autocomplete="off"
          :value="item.title"
          v-model="option.flavor"
          @click="(option.quantity = 1), (tempProduct = item)"
        />
        <label
          class="btn border-0 position-relative me-3"
          :for="`flavor${index}`"
        >
          {{ item.title }}
          <Circle1 :class="{ 'd-none': item.title !== option.flavor }" />
        </label>
      </template>
    </div>
    <!-- size -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.size') }}:</h4>
      <template v-for="(item, index) in tempProduct.size" :key="index">
        <input
          type="radio"
          class="btn-check"
          name="options-type"
          :id="`size${index}`"
          autocomplete="off"
          :value="item"
          v-model="option.size"
        />
        <label
          class="btn border-0 position-relative me-3"
          :for="`size${index}`"
        >
          {{ item }}
          <Circle2 :class="{ 'd-none': option.size !== item }" />
        </label>
      </template>
    </div>
    <!-- typeof cake -->
    <section v-if="tempProduct.type === 'cake'"></section>
    <!-- typeof drink -->
    <section v-else-if="tempProduct.type === 'drink'">
      <!-- ice -->
      <div class="mb-4">
        <h4>{{ $t('shoppingOptions.drink.ice') }}:</h4>
        <template v-for="(item, index) in iceList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`ice${index}`"
            autocomplete="off"
            :value="index"
            v-model="option.ice"
          />
          <label
            class="btn border-0 position-relative me-3"
            :for="`ice${index}`"
          >
            {{ $t(item.title) }}
            <Circle3 :class="{ 'd-none': index !== option.ice }" />
          </label>
        </template>
      </div>
      <div class="mb-4">
        <h4>{{ $t('shoppingOptions.drink.sugar') }}:</h4>
        <template v-for="(item, index) in sugarList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`sugar${index}`"
            autocomplete="off"
            :value="index"
            v-model="option.sugar"
          />
          <label
            class="btn border-0 position-relative me-3"
            :for="`sugar${index}`"
          >
            {{ $t(item.title) }}
            <Circle1 :class="{ 'd-none': index !== option.sugar }" />
          </label>
        </template>
      </div>
    </section>
    <!-- price -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.price') }}:</h4>
      <p class="ms-3">
        $
        <span class="text-secondary">{{ tempProduct.price }}</span>
        / {{ $t('shoppingOptions.each') }}
      </p>
    </div>
    <!-- num -->
    <div class="mb-4">
      <h4>{{ $t('shoppingOptions.quantity') }}:</h4>
      <nav aria-label="..." class="me-4">
        <ul class="pagination">
          <li class="page-item">
            <a
              class="page-link rounded-0"
              href="#"
              @click.prevent="option.quantity--"
              :class="{ disabled: option.quantity <= 1 }"
            >
              -
            </a>
          </li>
          <li class="page-item">
            <div class="page-link">{{ option.quantity }}</div>
          </li>
          <li class="page-item">
            <a
              class="page-link rounded-0"
              href="#"
              @click.prevent="option.quantity++"
              :class="{ disabled: option.quantity >= 10 }"
            >
              <i class="bi bi-plus-lg"></i>
            </a>
          </li>
        </ul>
      </nav>
    </div>
    <!-- 總金額 -->
    <div class="mb-4">
      <h4 class="text-uppercase">{{ $t('shoppingOptions.total') }}：</h4>
      <div class="fst-italic ms-3 h4 text-secondary">
        ${{ this.$toCurrency(tempProduct.price * option.quantity) }}
      </div>
    </div>
    <component :is="'wired-button'" class="bg-dark text-light h4 mb-0">
      {{ $t('shoppingOptions.addToChart') }}<i class="bi bi-plus-lg ms-2 text-light"></i>
    </component>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 5rem;
}
.circle-2 {
  width: 5rem;
}
.circle-3 {
  width: 5rem;
}
</style>

<script>
import 'wired-elements';
import Circle1 from '../helpers/focus/CircleOne.vue';
import Circle2 from '../helpers/focus/CircleTwo.vue';
import Circle3 from '../helpers/focus/CircleThree.vue';

export default {
  data() {
    return {
      tempProduct: {},
      tempProductList: [],
      types: [
        { title: 'shoppingOptions.typeList.cake', type: 'dessert' },
        { title: 'shoppingOptions.typeList.drink', type: 'drink' },
        { title: 'shoppingOptions.typeList.coffee', type: 'coffee' },
        { title: 'shoppingOptions.typeList.meal', type: 'meal' },
      ],
      iceList: [
        { title: 'shoppingOptions.drink.iceList.noIce' },
        { title: 'shoppingOptions.drink.iceList.lightIce' },
        { title: 'shoppingOptions.drink.iceList.lessIce' },
        { title: 'shoppingOptions.drink.iceList.normal' },
        { title: 'shoppingOptions.drink.iceList.warm' },
        { title: 'shoppingOptions.drink.iceList.hot' },
      ],
      sugarList: [
        { title: 'shoppingOptions.drink.sugarList.noSugar' },
        { title: 'shoppingOptions.drink.sugarList.lightSugar' },
        { title: 'shoppingOptions.drink.sugarList.halfSugar' },
        { title: 'shoppingOptions.drink.sugarList.lessSugar' },
        { title: 'shoppingOptions.drink.sugarList.standard' },
      ],
      option: {
        flavor: '',
        select: '',
        ice: 0,
        sugar: 0,
        price: 0,
        size: '',
        quantity: 1,
      },
    };
  },
  props: ['productList'],
  watch: {
    productList() {
      this.slelectProduct();
    },
    tempProduct() {
      this.$emitter.emit('productData', this.tempProduct);
    },
  },
  components: {
    Circle1,
    Circle2,
    Circle3,
  },
  methods: {
    slelectProduct(type = 'dessert') {
      this.tempProductList = [];
      this.productList.forEach((item) => {
        if (item.type === type) {
          this.tempProductList.push(item);
        }
      });
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.tempProductList[0];
      this.option = {
        flavor: this.tempProductList[0].title,
        ice: 0,
        sugar: 0,
        size: this.tempProductList[0].size[0],
        price: this.tempProductList[0].price,
        quantity: 1,
      };
    },
  },
  mounted() {
    this.slelectProduct();
  },
};
</script>
